import "./Headers.css"
import {Link} from "react-router-dom";
import {useState} from "react";
import {Menu} from "../Menu/Menu";


const Header = () => {

    const items = [{value: "Лиги" },{value: "Команды"}]
    const [MenuActive,setMenuActive] = useState(false)

    return (
        <div className="Header">
            <div className="header-left">
                <Link to="/">
                    <img className="header-logo"
                         src="data:image/png;base64,"/>
                </Link>
            </div>
            <div className="d-flex">
                <div className="header-right d-none d-sm-flex">
                    <Link to="/" className="header-content">Лиги</Link>
                    <Link to="/club" className="header-content">Команды</Link>
                </div>
                <span className="menu-burger material-icons-outlined d-flex d-sm-none" onClick={() => setMenuActive(!MenuActive)}  >
                menu
                </span>
            </div>
            <Menu active={MenuActive} setActive={setMenuActive} header={"Soccerstat"} items={items}/>
        </div>
    )

}
export default Header

