import React, {useState} from 'react';
import {Link} from "react-router-dom";
import "./CompetitionsCard.css"
import {Button,Modal} from "react-bootstrap";


const ball = "data:image/jpeg;base64,"


export const CompetitionsCard = ({country, image, name, plan, id}) => {

    // const [modalShow,setModalShow] = useState(true)
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <>
            <div  className="col-6 col-sm-4 col-md-3 mt-4 d-flex align-items-center flex-column justify-content-center">
                <h6  className="country-league">{country}</h6>
                <img  className="logo-league" src={image || ball}/>
                <Link to={plan === "TIER_ONE" ? `/competition/${id}` : {}} onClick={() => plan !== "TIER_ONE" ? handleShow() : {}}
                      className={plan === "TIER_ONE" ? "active-league" : "inactive-league"} >
                    {name}
                </Link>
            </div>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header className="popup-content" closeButton>
                    <Modal.Title>В данном тарифе эта Лига не доступна</Modal.Title>
                    <Modal.Title>Доступные Лиги начнуться со 2 странице :)</Modal.Title>
                </Modal.Header>
            </Modal>
        </>

    );
};


